import { PixelMapTerrain } from './PixelMapTerrain/PixelMapTerrain';
import * as THREE from 'three';

export abstract class GeometryWrapperBase {
    private static list: GeometryWrapperBase[] = [];

    static update(): void {
        for (let i = GeometryWrapperBase.list.length - 1; i >= 0; i--) {
            GeometryWrapperBase.list[i].update();
        }
    }

    constructor() {
        GeometryWrapperBase.list.push(this);
    }

    update(): void {
        this.updatePost();
    }

    remove(): void { /* */ }
    protected updatePost(): void { /* */ }
}

export class TerrainMapThree extends GeometryWrapperBase {
    private static instance: TerrainMapThree | undefined;

    private geometry: THREE.BufferGeometry;
    private mesh: THREE.Mesh;
    private pixelMapTerrain: PixelMapTerrain;
    private img: HTMLImageElement;
    private scene: THREE.Scene;

    static shiftShader(): void {
        TerrainMapThree.instance?.shiftShader();
    }

    constructor(scene: THREE.Scene) {
        super();
        this.scene = scene;
        this.loadImage();
    }

    private async loadImage(): Promise<void> {
        // const file = 'fragment.png';
        // const imagePath = `njslabSandboxStatic/img/${file}`;

        // const imagePath = 

        const path1 = 'https://upload.wikimedia.org/wikipedia/commons/1/15/Srtm_ramp2.world.21600x10800.jpg';
        const path2 = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/heightMap/Heightmap_01.png';
        const path3 = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/heightMap/Heightmap_02.jpg';
        const path4 = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/heightMap/Heightmap_03.jpg';

        try {
            this.img = await this.loadImageAsync(path3);
            this.initDataStructure();
            this.initGeometry();
            this.initMesh();
        } catch (error) {
            console.error('Failed to load image:', error);
        }
    }

    private loadImageAsync(src: string): Promise<HTMLImageElement> {
        return new Promise((resolve, reject) => {
            const img = new Image();
            img.onload = () => resolve(img);
            img.onerror = reject;
            img.src = src;
        });
    }

    private initDataStructure(): void {
        console.log(this.img.width);
        this.pixelMapTerrain = new PixelMapTerrain(this.img, 40, 40, 1, 1, 0.2);
    }

    private initGeometry(): void {
        const vs = this.pixelMapTerrain.GetTriFloatArray();
        const vertices = new Float32Array(vs);
        const indices = [];

        for (let i = 0; i < vertices.length / 3 - 2; i += 3) {
            indices.push(i, i + 1, i + 2);
        }

        this.geometry = new THREE.BufferGeometry();
        this.geometry.setAttribute('position', new THREE.BufferAttribute(vertices, 3));
        this.geometry.setIndex(indices);
        this.geometry.rotateX(Math.PI * 0.5);
        this.geometry.computeVertexNormals();
    }

    private initMesh(): void {
        this.mesh = new THREE.Mesh(this.geometry, new THREE.MeshNormalMaterial());
        this.mesh.scale.set(0.04, 0.04, 0.04);
        this.mesh.position.y = -1;
        this.scene.add(this.mesh);
    }

    remove(): void {
        TerrainMapThree.instance = undefined;
        this.scene.remove(this.mesh);
    }

    shiftShader(): void {
        // Implement shader shifting logic here if needed
    }

    protected updatePost(): void {
        // Implement update logic here if needed
    }
}