import * as THREE from 'three';
import { NTerrain } from '..';
import { GeometryCommon } from './GeometryCommon/GeometryCommon';
import { NTerrain3DField } from './Definition';
import { TerrainMapThree } from './Definition/NTrain/TerrainMapThree';
export class SceneWrapper {
    app: NTerrain;
    geometryCommon: GeometryCommon;
    scene: THREE.Scene;
    definition: NTerrain3DField;
    constructor(app: NTerrain) {
        this.app = app;
        this.scene = this.app.graphicsCoreWrapper.canvasWrapper.scene;
        this.app.graphicsCoreWrapper.canvasWrapper.UpdateBinding = () => { this.Update(); };
        this.geometryCommon = new GeometryCommon(this);
        this.Init();
    }
    public Init() {
        this.definition = new NTerrain3DField(this);
        this.scene.add(this.definition.mesh);

        const dd = new TerrainMapThree(this.scene);
        console.log(dd);
    }
    public Update() {
        // console.log('loop...');
    }
}