/////////////////// PixelMapTerrain class
export class PixelMapTerrain {
    public static img: HTMLImageElement;
    public ctx: CanvasRenderingContext2D;

    public img: HTMLImageElement;
    public listPixel: Pixel[] = [];
    public width: number;
    public height: number;
    public rx: number = 0;
    public ry: number = 0;
    public sx: number = 0;
    public sy: number = 0;
    public sz: number = 0;
    public ddx: number = 0.0;
    public ddy: number = 0.0;
    public v0: NVector3;
    public v1: NVector3;

    constructor(img: HTMLImageElement, rx: number = 250, ry: number = 250, sx: number = 1, sy: number = 1, sz: number = 1) {
        this.img = img;
        console.log(img);

        let canvas = document.createElement('canvas') as HTMLCanvasElement;
        canvas.width = this.img.width * sx;
        canvas.height = this.img.height * sy;
        this.ctx = canvas.getContext('2d') as CanvasRenderingContext2D;
        // this.img.width = this.img.width * sx;
        // this.img.height = this.img.height * sy;
        this.ctx.drawImage(this.img, 0, 0);

        this.width = this.img.width;
        this.height = this.img.height;

        console.log(this.img.width, this.img.height, 'lllllllllllllll');
        this.rx = rx; this.ry = ry;
        this.sx = sx; this.sy = sy; this.sz = sz;
        this.ddx = 0.0;
        this.ddy = 0.0;
        this.v0 = new NVector3(-this.width * 0.5 * this.sx, -this.img.height * this.sy * 0.5, 0);
        this.v1 = new NVector3(this.width * 0.5 * this.sx, this.img.height * this.sy * 0.5, 0);

        this.Init();
    }
    Init() {
        this.ddx = this.width * this.sx / (this.rx - 1.0);
        this.ddy = this.height * this.sy / (this.ry - 1.0);
        console.log('ddx is ' + this.ddx + '. ddy is ' + this.ddx);
        console.log('Pixe Map was executed...');
        this.InitGrid();
    }
    InitGrid() {
        console.log(this);
        let vv = 0;
        for (let y = 0; y < this.ry; ++y) {
            let uu = 0;
            for (let x = 0; x < this.rx; ++x) {

                let px = new Pixel(this);

                px.i = x; px.j = y;
                px.u = uu; px.v = vv;

                let theX = this.v0.x + (this.ddx * x);
                let theY = this.v0.y + (this.ddy * y);

                let dd = this.ctx.getImageData((this.ddx * x / this.sx), (this.ddx * y / this.sy), 1, 1).data;
                let zVal = ColorUtility.RGBtoGray(dd[0], dd[1], dd[2]);
                let theZ = -zVal * this.sz;

                let pos = [theX, theY, theZ];
                px.vec = new NVector3(pos[0], pos[1], pos[2]);
                px.norV = NVector3.Normalize(px.vec);

                pos = [theX - this.ddx * 0.5, theY - this.ddy * 0.5, theZ];
                px.recVec.push(new NVector3(pos[0], pos[1], pos[2]));

                pos = [theX + this.ddx * 0.5, theY - this.ddy * 0.5, theZ];
                px.recVec.push(new NVector3(pos[0], pos[1], pos[2]));

                pos = [theX + this.ddx * 0.5, theY + this.ddy * 0.5, theZ];
                px.recVec.push(new NVector3(pos[0], pos[1], pos[2]));

                pos = [theX - this.ddx * 0.5, theY + this.ddy * 0.5, theZ];
                px.recVec.push(new NVector3(pos[0], pos[1], pos[2]));

                // px.recVec.push(new NVector3(xx - this.ddx * 0.5, yy - this.ddy * 0.5, zz));
                // px.recVec.push(new NVector3(xx + this.ddx * 0.5, yy - this.ddy * 0.5, zz));
                // px.recVec.push(new NVector3(xx + this.ddx * 0.5, yy + this.ddy * 0.5, zz));
                // px.recVec.push(new NVector3(xx - this.ddx * 0.5, yy + this.ddy * 0.5, zz));
                // console.log(zz);

                this.listPixel.push(px);
                uu++;
            }
            vv++;
        }
        // console.log(this.listPixel);
        this.InitGridNeighbor();
    }
    InitGridNeighbor() {
        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                let k = j * this.rx + i;
                let px0 = this.listPixel[k];
                px0.nPix = [];
                if (i > 0) { px0.nPix.push(this.listPixel[k - 1]); }
                if (i < this.rx - 1) { px0.nPix.push(this.listPixel[k + 1]); }
                if (j > 0) { px0.nPix.push(this.listPixel[k - this.rx]); }
                if (j < this.ry - 1) { px0.nPix.push(this.listPixel[k + this.rx]); }
                if ((i > 0) && (j < this.ry - 1)) { px0.nPix.push(this.listPixel[k + this.rx - 1]); }
                if ((i > 0) && (j > 0)) { px0.nPix.push(this.listPixel[k - this.rx - 1]); }
                if ((i < this.rx - 1) && (j < this.ry - 1)) { px0.nPix.push(this.listPixel[k + this.rx + 1]); }
                if ((i < this.rx - 1) && (j > 0)) { px0.nPix.push(this.listPixel[k - this.rx + 1]); }
            }
        }
        console.log('done for computing neighbors ');
    }
    GetPixelAtPoint(pt: NVector3) {
        let pixelMap = this;
        let dp = new NVector3(pt.x - pixelMap.v0.x, pt.y - pixelMap.v0.y, pt.z - pixelMap.v0.z);
        let i = Math.round(dp.x / pixelMap.ddx);
        let j = Math.round(dp.y / pixelMap.ddy);
        if (i >= pixelMap.rx) { i = pixelMap.rx - 1; }
        if (j >= pixelMap.ry) { j = pixelMap.ry - 1; }
        if (i < 0) { i = 0; }
        if (j < 0) { j = 0; }
        return pixelMap.listPixel[j * pixelMap.rx + i];
    }
    ////////////////// event
    MouseMove(x: number, y: number, z: number) {
        let pixelMap = this;
        console.log('mouseMove x:' + x + ', y:' + y + ', z:' + z);
        let cpt = pixelMap.GetPixelAtPoint(new NVector3(x, y, z));
        console.log(cpt.nPix);
        // VisBufferPointsFromVecs(cpt.ptsCenter, 100);
    }
    onDocumentMouseMove() {
        console.log('move');
    }
    onmouseenter() {
        console.log('enter');
    }
    ////////////////// visualization
    CVisLineGrid(ctx: CanvasRenderingContext2D) {
        let pixelMap = this;
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            let color = '#ff0000';
            // console.log('d');
            ctx.lineWidth = 0.5;
            ctx.beginPath();
            ctx.moveTo(pixelMap.listPixel[i].recVec[0].x, pixelMap.listPixel[i].recVec[0].y);
            // console.log(pixelMap.listPixel[i].recVec)
            for (let j = 1; j < pixelMap.listPixel[i].recVec.length; ++j) {
                ctx.lineTo(pixelMap.listPixel[i].recVec[j].x, pixelMap.listPixel[i].recVec[j].y);
                // console.log(pixelMap.listPixel[i].nPix[j].vec.x, pixelMap.listPixel[i].nPix[j].vec.y);

            }
            ctx.closePath();
            ctx.strokeStyle = color; // ColorUtility.HexToCanvasColor(color, 1);
            ctx.stroke();
            // ctx.fill();
        }
    }
    VisMeshGrid() {
        let pixelMap = this;
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            // VisMeshRectFromFourVecs(pixelMap.listPixel[i].ptsRectLst);
        }
    }
    VisCenterPoints() {
        let pixelMap = this;
        let pts: NVector3[] = [];
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            pts.push(pixelMap.listPixel[i].vec);
        }
        // VisBufferPointsFromVecs(pts);
    }
    Render(ctx: CanvasRenderingContext2D) {
        /* */
    }
    GetTriFloatArray() { return new Float32Array(this.GetTriArray()); }
    GetTriArray() {
        let array: number[] = [];
        // for (let i = 0; i < this.listPixel.length; ++i) {
        //     p = this.listPixel[i].recVec[0]; array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[3]; array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[2]; array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[0]; array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[2]; array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[1]; array.push(p.x, p.y, p.z);

        // }
        for (let j = 0; j < this.ry - 1; ++j) {
            let p = new Pixel(this, NVector3.Origin());
            for (let i = 0; i < this.rx - 1; ++i) {
                let k = j * this.rx + i;
                p = this.listPixel[k]; array.push(p.vec.x, p.vec.y, p.vec.z);
                p = this.listPixel[k + this.rx]; array.push(p.vec.x, p.vec.y, p.vec.z);
                p = this.listPixel[k + this.rx + 1]; array.push(p.vec.x, p.vec.y, p.vec.z);
                p = this.listPixel[k]; array.push(p.vec.x, p.vec.y, p.vec.z);
                p = this.listPixel[k + this.rx + 1]; array.push(p.vec.x, p.vec.y, p.vec.z);
                p = this.listPixel[k + 1]; array.push(p.vec.x, p.vec.y, p.vec.z);

            }
        }
        return array;
    }
    GetPtFloatArray() { return new Float32Array(this.GetPtArray()); }
    GetPtArray() {
        let array: number[] = [];
        for (let i = 0; i < this.listPixel.length; ++i) {
            let p = this.listPixel[i].vec;
            array.push(p.x, p.y, p.z);
        }
        return array;
    }
    GetNormalFloatArray() { return new Float32Array(this.GetNormalArray()); }
    GetNormalArray() {
        let array: number[] = [];

        // let p = NVector3.origin;
        // for (let i = 0; i < this.listPixel.length; ++i) {
        //     p = this.listPixel[i].recVec[0]; p.Normalize(); array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[3]; p.Normalize(); array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[2]; p.Normalize(); array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[0]; p.Normalize(); array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[2]; p.Normalize(); array.push(p.x, p.y, p.z);
        //     p = this.listPixel[i].recVec[1]; p.Normalize(); array.push(p.x, p.y, p.z);
        // }

        let p = new Pixel(this, NVector3.Origin());
        let v = new NVector3(0, 0, 0);
        for (let j = 0; j < this.ry - 1; ++j) {
            for (let i = 0; i < this.rx - 1; ++i) {
                let k = i + j * this.rx;
                p = this.listPixel[k]; v.x = p.vec.x; v.y = p.vec.y; v.z = p.vec.z; v.Normalize(); array.push(v.x, v.y, v.z);
                p = this.listPixel[k + this.rx]; v.x = p.vec.x; v.y = p.vec.y; v.z = p.vec.z; v.Normalize(); array.push(v.x, v.y, v.z);
                p = this.listPixel[k + this.rx + 1]; v.x = p.vec.x; v.y = p.vec.y; v.z = p.vec.z; v.Normalize(); array.push(v.x, v.y, v.z);
                p = this.listPixel[k]; v.x = p.vec.x; v.y = p.vec.y; v.z = p.vec.z; v.Normalize(); array.push(v.x, v.y, v.z);
                p = this.listPixel[k + this.rx + 1]; v.x = p.vec.x; v.y = p.vec.y; v.z = p.vec.z; v.Normalize(); array.push(v.x, v.y, v.z);
                p = this.listPixel[k + 1]; v.x = p.vec.x; v.y = p.vec.y; v.z = p.vec.z; v.Normalize(); array.push(v.x, v.y, v.z);

            }
        }

        return array;
    }
    GetTexCoordinFloatArray() { return new Float32Array(this.GetTexCoordinArray()); }
    GetTexCoordinArray() {
        let array: number[] = [];
        // let p = NVector3.origin;

        for (let i = 0; i < this.listPixel.length; ++i) {
            let u = this.listPixel[i].u / (this.rx - 1);
            let v = this.listPixel[i].v / (this.ry - 1);
            array.push(u, v);
            array.push(u, v + this.ddy);
            array.push(u + this.ddx, v + this.ddy);
            array.push(u, v);
            array.push(u + this.ddx, v + this.ddy);
            array.push(u + this.ddx, v);
            // px.recVec.push(new NVector3(xx - this.ddx * 0.5, yy - this.ddy * 0.5, zz));
            // px.recVec.push(new NVector3(xx + this.ddx * 0.5, yy - this.ddy * 0.5, zz));
            // px.recVec.push(new NVector3(xx + this.ddx * 0.5, yy + this.ddy * 0.5, zz));
            // px.recVec.push(new NVector3(xx - this.ddx * 0.5, yy + this.ddy * 0.5, zz));
        }

        return array;
    }
}